// External services

//Common
import GenderService from "@/services/common/gender.service";
import CountryService from "@/services/common/country.service"
import ProductTypeService from "@/services/common/product_type.service";
import CurrencyService from "@/services/common/currency.service";

//User
import ClientService from "@/services/user/client.service"
import RolesService from "@/services/user/roles.service"

//Pole
import PoleService from "@/services/pole/pole.service"
import PoleTypeService from "@/services/pole/pole_type.service"

// Hotel Services
import HotelService from "@/services/hotel/hotel.service"
import HotelCategoryService from "@/services/hotel/hotel_category.service"
import HotelChainService from "@/services/hotel/hotel_chain.service"
import HotelLocationService from "@/services/hotel/hotel_location.service"
import HotelRoomService from "@/services/hotel/hotel_room.service"
import HotelRoomTypeService from "@/services/hotel/hotel_room_type.service"
import HotelRoomTypeCategoryService from "@/services/hotel/hotel_room_type_category.service"
import HotelSegmentService from "@/services/hotel/hotel_segment.service"

//Rate
import RateOfferService from "@/services/rate/rate_offer.service"
import RateTypeService from "@/services/rate/rate_type.service"

//Gallery
import GalleryService from "./gallery/gallery.service";
import GalleryFilterService from "./gallery/gallery_filter.service";
import GalleryPhotoFilterService from "./gallery/gallery_photo_filter.service";

export default {
    // External services
    ClientService: ClientService,
    RolesService: RolesService,
    PoleService: PoleService,
    PoleTypeService: PoleTypeService,
    CountryService: CountryService,
    CurrencyService: CurrencyService,
    GenderService: GenderService,
    ProductTypeService: ProductTypeService,
    // CrudGallery
    // CrudGalleryFilter
    // CrudGalleryPhotoFilter
    // CrudGalleryI18n

    // Hotel Services
    HotelService: HotelService,
    HotelCategoryService: HotelCategoryService,
    HotelChainService: HotelChainService,
    HotelRoomService: HotelRoomService,
    // CrudHotelRoomAllotment
    HotelLocationService: HotelLocationService,
    // CrudHotelRoomPrice
    HotelRoomTypeService: HotelRoomTypeService,
    HotelRoomTypeCategoryService: HotelRoomTypeCategoryService,
    HotelSegmentService: HotelSegmentService,
    RateOfferService: RateOfferService,
    RateTypeService: RateTypeService,
    // GalleryService
    GalleryService: GalleryService,
    GalleryFilterService: GalleryFilterService,
    GalleryPhotoFilterService: GalleryPhotoFilterService,

    init: async(param) => {
        let result
        switch (param) {
            case 'client':
                result = await ClientService.getList()
                break;

            case 'roles':
                result = await RolesService.getList()
                break;

            case 'pole':
                result = await PoleService.getList()
                break;

            case 'pole_type':
                result = await PoleTypeService.getList()
                break;

            case 'country':
                result = await CountryService.getList()
                break;

            case 'currency':
                result = await CurrencyService.getList()
                break;

            case 'gender':
                result = await GenderService.getList()
                break;

            case 'product_type':
                result = await ProductTypeService.getList()
                break;

            // CrudGallery
            case 'gallery': 
                result = await GalleryService.getList()
                break;
            
            case 'gallery_filter': 
                result = await GalleryFilterService.getList()
                break;

            case 'gallery_photo_filter': 
                result = await GalleryPhotoFilterService.getList()
                break;

            case 'hotel':
                result = await HotelService.getList()
                break;

            case 'hotel_category':
                result = await HotelCategoryService.getList()
                break;

            case 'hotel_chain':
                result = await HotelChainService.getList()
                break;

            case 'hotel_room':
                result = await HotelRoomService.getList()
                break;

                // CrudHotelRoomAllotment

            case 'hotel_location':
                result = await HotelLocationService.getList()
                break;

                // CrudHotelRoomPrice

            case 'hotel_room_type':
                result = await HotelRoomTypeService.getList()
                break;

            case 'hotel_room_type_category':
                result = await HotelRoomTypeCategoryService.getList()
                break;

            case 'hotel_segment':
                result = await HotelSegmentService.getList()
                break;

            case 'rate_offer':
                result = await RateOfferService.getList()
                break;

            case 'rate_type':
                result = await RateTypeService.getList()
                break;

            default:
                break;
        }
        return result
    },
    delete: async(param, ids) => {
        let result
        switch (param) {
            case 'client':
                result = await ClientService.delete(ids)
                break;

            case 'roles':
                result = await RolesService.delete(ids)
                break;

            case 'pole':
                result = await PoleService.delete(ids)
                break;

            case 'pole_type':
                result = await PoleTypeService.delete(ids)
                break;

            case 'country':
                result = await CountryService.delete(ids)
                break;

            case 'currency':
                // console.log(ids)
                result = await CurrencyService.delete(ids)
                break;

            case 'gender':
                result = await GenderService.delete(ids)
                break;

            case 'product_type':
                result = await ProductTypeService.delete(ids)
                break;

                // CrudGallery
                // CrudGalleryFilter
                // CrudGalleryPhotoFilter
                // CrudGalleryI18n

            case 'hotel':
                result = await HotelService.delete(ids)
                break;

            case 'hotel_category':
                result = await HotelCategoryService.delete(ids)
                break;

            case 'hotel_chain':
                result = await HotelChainService.delete(ids)
                break;

            case 'hotel_room':
                result = await HotelRoomService.delete(ids)
                break;

                // CrudHotelRoomAllotment

            case 'hotel_location':
                result = await HotelLocationService.delete(ids)
                break;

                // CrudHotelRoomPrice

            case 'hotel_room_type':
                result = await HotelRoomTypeService.delete(ids)
                break;

            case 'hotel_room_type_category':
                result = await HotelRoomTypeCategoryService.delete(ids)
                break;

            case 'hotel_segment':
                result = await HotelSegmentService.delete(ids)
                break;

            case 'rate_offer':
                result = await RateOfferService.delete(ids)
                break;

            case 'rate_type':
                result = await RateTypeService.delete(ids)
                break;

            case 'gallery': 
                result = await GalleryService.delete(ids)
                break;
            
            case 'gallery_filter': 
                result = await GalleryFilterService.delete(ids)
                break;

            case 'gallery_photo_filter': 
                result = await GalleryPhotoFilterService.delete(ids)
                break;

            default:
                break;
        }
        return result
    }
}