import CountryService from "@/services/common/country.service";

export default {
    props: {
        info: {},        
        mode: {
            default: 'create'
        }
    },    
    name: "CrudCountry",
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            columns: [],
            formData: {
            //general info
                id: '',
                code: '',
                alias: '',
                coin: '',                            
            },
        }
    },
    created() {   
        this.initForm()     
    },
    watch: {
        mode() {
            this.initForm()
        },             
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        },
        tableColumns() {
            const newCols = Object.keys(this.tableInfo[0])            
            this.columns = newCols
            if (this.columns[0] == 'id') this.columns.shift()
            const last = this.columns.pop();
            if(last != 'actions'){
               this.columns.push(last);
               this.columns.push('actions');
            }
            else {
               this.columns.push('actions')            
            }
            return this.columns
        },             
    },
    methods: {            
        initForm() {
            // For upd action

            CountryService.getCountry(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.code = e.code
                    this.formData.alias = e.alias
                    this.formData.coin = e.coin
                }
            })
        },   
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },        
        submitForm(e){
            e.preventDefault();            
            if(this.validate(this.formData.code) || this.validate(this.formData.alias) ) {   
                                
                CountryService.addUpdCountry(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
                
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getCountry(e) {
            console.log(e)
        },
    }
}
