import HotelRoomTypeCategoryService from "@/services/hotel/hotel_room_type_category.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },     
    name: "CrudHotelRoomTypeCategory",
    components: {
        },
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            load: false,
            formData: {
                //general info
                id: '',
                name: '',
            },
        }
    },
    created() {
        this.initForm()
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods:{
        initForm() {
            // For upd action      
            HotelRoomTypeCategoryService.getHotelRoomTypeCategory(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.name = e.name
                }
            })          
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            e.preventDefault();                
            if(this.validate(this.formData.name) ) {        
                this.load = true        
                HotelRoomTypeCategoryService.addUpdHotelRoomTypeCategory(this.formData, (this.mode == 'create')).then(res => {
                    this.load = false
                    console.log(res)
                    if(res) {
                        this.error_message = false
                        this.success_message = true                        
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getHotelRoomTypeCategory(e) {
            console.log(e)
        },
    }   
}
