import RateTypeService from "@/services/rate/rate_type.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudRateType",
    components: {        
    },
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,

            formData: {
                //general info
                id: '',
                active: '',
                name: '',
                description: '',
            },
        }
    },
    created() {
        // Init country select
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            RateTypeService.getRateType(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.name = e.name
                    this.formData.active = e.active
                    this.formData.description = e.description
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();            
            if(this.validate(this.formData.name) ) {   
                /* this.formData.parent_id = this.formData.parent
                this.formData.country_id = this.formData.country  */               
                
                RateTypeService.addUpdRateType(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getRateType(e) {
            console.log(e)
        },
    }
}
