import authService from "@/services/user/auth.service";

export default {  
  name: "Header",  
  data() {
    return {
      openMenu: false,
      showNavMobile: false,
      isLogged: false,
      user_name: ''
    };
  },
  created() {
    const token = authService.getClientToken()
    this.user_name = token.client.name
  },  
  methods: {
    async logout() {
      authService.logout().then(e => {
        this.$router.push({name: 'Login'})
      })
    }
  },
};
