import CurrencyService from "@/services/common/currency.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudCurrency",
    components: {        
    },
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,            
            formData: {
                //general info
                id: '',
                name: '',
                code: '',
                iso_code: '',
                symbol: '',
                rate: '',
                active: '',                
            },
        }
    },
    created() {    
        this.initForm()    
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            CurrencyService.getCurrency(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.code = e.code
                    this.formData.iso_code = e.iso_code
                    this.formData.name = e.name
                    this.formData.symbol = e.symbol
                    this.formData.rate = e.rate
                    this.formData.active = e.active
                }
            })
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            //console.log(this.formData)
            e.preventDefault();            
            if(this.validate(this.formData.code) || this.validate(this.formData.name) || this.validate(this.formData.rate)) {                
                
                CurrencyService.addUpdCurrency(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getCurrency(e) {
            console.log(e)
        },
    }    
}
