import MapZone from '@/components/Maps/MapZone'
import {Tabs, Tab} from 'vue-tabs-component'
import '@/assets/css/tabs.css'

import AdminTable from '@/components/Admin/AdminTable';
import '@/assets/css/table.css'

import { ModelSelect } from 'vue-search-select'
import { MultiSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import PoleService from "@/services/pole/pole.service";
import HotelService from "@/services/hotel/hotel.service"
import HotelCategoryService from "@/services/hotel/hotel_category.service"
import HotelChainService from "@/services/hotel/hotel_chain.service"
import HotelLocationService from "@/services/hotel/hotel_location.service"
import HotelSegmentService from "@/services/hotel/hotel_segment.service"

export default {
    props: {
        info: {},
        crud: true,
        mode: {
            default: 'create'
        }
    },
    name: "CrudHotel",    
    components: {
        Tabs,
        Tab,
        AdminTable,      
        ModelSelect,
        MultiSelect,
        MapZone
    },
    data: () => ({          
        showForm: true,
        info_message: '',
        poles: [],
        categories: [],
        chains: [],
        locations: [],
        hotelrooms: [],
        segments: [],
        form: {
            alias: '',
            pole_id: {
                value: '',
                text: ''
            },
            location_id: {
                value: '',
                text: ''
            },
            chain_id: {
                value: '',
                text: ''
            },
            category_id: {
                value: '',
                text: ''
            },
            segments: [],
            stars: '',
            keyword: '',
            logo: '',
            order: '',
            address: '',
            in_special_offer: '',
            phone: '',
            email: '',
            price_from: '',
            build_year: '',
            restored_year: '',
            url: '',
            buildings: '',
            houses: '',
            bungalows: '',
            handicapped_services: '',
            children_allowed: '',
            check_in: '',
            check_out: '',
            active: '',
            in_offer: '',
            longitude: '',
            latitude: '',
            published: '',

        },        
    }),
    created() { 
        let data =[]
        data.push(PoleService.getSimpleList())//.then(c => this.poles = c)
        data.push(HotelCategoryService.getSimpleList())//.then(c => this.categories = c)
        data.push(HotelChainService.getSimpleList())//.then(c => this.chains = c)
        data.push(HotelLocationService.getSimpleList())//.then(c => this.locations = c)
        data.push(HotelSegmentService.getSimpleList())//.then(c => this.segments = c)      
        
        Promise.all(data).then(d => {
            // console.log(d)
            this.poles = d[0]
            this.categories = d[1]
            this.chains = d[2]
            this.locations = d[3]
            this.segments = d[4]
            
            this.validateInitInfo()
        }) 
        
        if(this.mode != 'create') { 
            HotelService.getRoomList(this.info).then(c => {            
                for(let r of c) {
                    this.hotelrooms.push({
                        id: r.id,
                        alias: r.alias,
                        active: (r.active) ? true : false,
                        order: r.order
                    })
                }
            }) 
            this.initForm()
        }
    },    
    methods:{
        validateInitInfo() {
            if(this.poles.length == 0 || this.categories.length == 0 || this.chains.length == 0 ||
            this.locations.length == 0 || this.segments.length == 0 ) {
                this.showForm = false
                this.info_message = (this.poles.length == 0) ? 'Polos <br>'  : ''
                this.info_message += (this.categories.length == 0) ? ' Caegorias de hotel <br>' : ''
                this.info_message += (this.chains.length == 0) ? ' Cadenas hoteleras <br>' : ''
                this.info_message += (this.locations.length == 0) ? ' Locaciones <br>' : ''
                this.info_message += (this.segments.length == 0) ? ' Segmentos <br>' : ''
            }
        },
        initForm() {
            HotelService.getHotel(this.info).then(e => {
                if(this.mode != 'create') {
                    this.form.id = e.id
                    this.form.alias = e.alias
                    this.form.pole_id = e.pole_id
                    this.form.location_id = e.location_id
                    this.form.chain_id = e.chain_id
                    this.form.category_id = e.category_id
                    this.form.stars = e.stars
                    this.form.keyword = e.keyword
                    this.form.logo = e.logo
                    this.form.order = e.order
                    this.form.address = e.address
                    this.form.in_special_offer = e.in_special_offer
                    this.form.phone = e.phone
                    this.form.email = e.email
                    this.form.price_from = e.price_from
                    this.form.build_year = e.build_year
                    this.form.restored_year = e.restored_year
                    this.form.url = e.url
                    this.form.buildings = e.buildings
                    this.form.houses = e.houses
                    this.form.bungalows = e.bungalows
                    this.form.rooms = e.rooms
                    this.form.handicapped_services = e.handicapped_services
                    this.form.children_allowed = e.children_allowed
                    this.form.check_in = e.check_in
                    this.form.check_out = e.check_out
                    this.form.active = e.active
                    this.form.in_offer = e.in_offer
                    for(let s of e.hotel_segments) {
                        this.form.segments.push({
                                text: s.segment,
                                value: s.segment_id
                            })
                    }
                    // for(let s of e.hotel_segments) {
                    //     this.form.segments.push({
                    //             text: s.segment,
                    //             value: s.segment_id
                    //         })
                    // }
                }
            })
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            e.preventDefault(); 
            if(this.validate(this.form.alias) || this.validate(this.form.pole_id) || this.validate(this.form.location_id) || this.validate(this.form.chain_id)
            || this.validate(this.form.category_id) || this.validate(this.form.keyword)) {   
                // Fix segment list
                const formData = this.form
                const segment_list = []
                for(let s of this.form.segments){
                    segment_list.push(s.value)
                }                
                formData.segments = segment_list                
                HotelService.addUpdHotel(formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.$emit('actionInfo', {
                            text: '',
                            type: 'success'
                        })                        
                    }
                })
            } else {
                this.$emit('actionInfo', {
                    text: 'Faltan datos por llenar',
                    type: 'error'
                })
            }
        },
        onSegmentSelect(items, litem){
            this.form.segments = items
        },
        onRoomSelect(items, litem){
            this.form.hotel_rooms = items
        },
        handeClick(newTab){
            this.currentTab = newTab;
        },
        editRoom(rowData) {
            // Showing Form      
            console.log(rowData)
            // this.crudvisible = !this.crudvisible          
            // const is_new = false
            // this.mode = 'update'          
            // this.formInfo = rowData.id
        },
    },
}
