// import ChoicePointMap from '@/components/Maps/ChoicePointMap';
import CountryService from "@/services/common/country.service";
import PoleService from "@/services/pole/pole.service";
import PoleTypePoleService from "@/services/pole/pole_type_pole.service";
import { MultiSelect } from 'vue-search-select';
import FileUploadService from "@/services/fileupload.service";
import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudPole",
    components: {
        // ChoicePointMap,
        MultiSelect,
        ModelSelect,
    },
    data() {
        return {
            selectedFile: null,
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            info_message: '',
            error_message_text: '',
            error_message: false,
            success_message: false,
            showForm: true,
            pole_types: [],
            countries: [],
            parents: [],
            formData: {
                //general info
                id: '',
                // images:[],
                order: '',
                alias: '',
                keyword: '',
                codsv: '',
                longitude: '',
                latitude: '',
                active: '',
                parent_id: '',
                parent: {
                    value: '',
                    text: ''
                },
                country_id: '',
                country: {
                    value: '',
                    text: ''
                },
                pole_types_id: [],
                pole_types: [],
            },
        }
    },
    created() {
        // Init country select
        let data =[]
        data.push(PoleService.getSimpleList())//.then(c => this.parents = c)
        data.push(CountryService.getSimpleList())//.then(c => this.countries = c)
        data.push(PoleTypePoleService.getSimpleList())//.then(c => this.pole_types = c)
        
        Promise.all(data).then(d => {
            // console.log(d)
            this.parents = d[0]
            this.countries = d[1]
            this.pole_types = d[2]
            this.validateInitInfo()
        })        

        this.initForm()
    },           
    methods:{
        validateInitInfo() {

            // Si combos vacios redirigir a tablas que hay que llenar 
            if(this.countries.length === 0 || this.pole_types.length === 0) {
                this.showForm = false
                this.info_message += (this.countries.length == 0) ? ' Paises <br>' : '',
                this.info_message += (this.pole_types.length == 0) ? ' Tipos de Polos <br>' : ''
            }
        },
        initForm() {
            // For upd action      
            PoleService.getPole(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.order = e.order
                    this.formData.alias = e.alias
                    this.formData.keyword = e.keyword
                    this.formData.codsv = e.codsv
                    this.formData.longitude = e.longitude
                    this.formData.latitude = e.latitude
                    this.formData.active = e.active
                    this.formData.parent = e.parent_id
                    this.formData.country = e.country_id
                    this.formData.images = e.images
                    for(let s of e.pole_types) {
                        this.formData.pole_types_id.push({
                                text: s.alias,
                                value: s.id
                            })
                    }
                }
            })          
        },               
        onPoleTypesSelect(items, litem){
            this.formData.pole_types_id = items
        },
        onFileSelected(event){
            this.selectedFile = this.$refs.file.files[0]
            // console.log(this.selectedFile)
        },
        onUpload(){
            const form_img = new FormData();
            form_img.append('image',this.selectedFile,this.selectedFile.name)
            this.formData.images = form_img
            //FileUploadService.upload(formdata)
            // console.log(this.formData.images)
        },       
        submitForm(e) {
            e.preventDefault();            
            //console.log(this.formData) 
            if(this.validate(this.formData.order) || this.validate(this.formData.alias) || this.validate(this.formData.active) ||
            this.validate(this.formData.parent) || this.validate(this.formData.country)) { 
                                
                this.formData.pole_types_id.map(e => this.formData.pole_types.push(e.value))
                this.formData.parent_id = this.formData.parent.value
                this.formData.country_id = this.formData.country                
                
                //! upload image
                // if(this.selectedFile)
                //     this.onUpload()
                    
                const fd = new FormData()

                // agrgando imagen
                if(this.selectedFile)
                    fd.append('images',this.selectedFile,this.selectedFile.name)

                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }
                // console.log(fd)
                PoleService.addUpdPole({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = truenpm 
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }                    
                })
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        }, 
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    }, 
}
