import { ModelSelect } from 'vue-search-select'
import ProductService from "@/services/rate/rate_offer.service";
import ProductTypeService from "@/services/common/product_type.service";
import RateOfferService from "@/services/rate/rate_offer.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudRateOffer",
    components: {        
        ModelSelect,
    },
    data() {
        return {
            error_message_text: '',
            error_message: false,
            success_message: false,

            product_types: [],
            products: [],

            formData: {
                //general info
                id: '',
                discount: '',
                bookingw_ini: '',
                bookingw_end: '',
                early_booking_days: '',
                min_nights: '',
                travelw_ini: '',
                travelw_end: '',
                
                product_type_id: '',
                product_type: {
                    value: '',
                    text: ''
                },

                product_id: '',
                product: {
                    value: '',
                    text: ''
                },
            },
        }
    },
    created() {
        // Init country select
        
        //ProductService.getSimpleList().then(c => this.products = c)
        //ProductTypeService.getSimpleList().then(c => this.product_types = c)
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action                
            if(this.mode != 'create') {
                this.formData.id = this.info.id
                this.formData.discount = this.info.discount
                this.formData.product_type_id = this.info.product_type_id
                this.formData.product_id = this.info.product_id
                this.formData.bookingw_ini = this.info.bookingw_ini
                this.formData.bookingw_end = this.info.bookingw_end
                this.formData.early_booking_days = this.info.early_booking_days
                this.formData.min_nights = this.info.min_nights
                this.formData.travelw_ini = this.info.travelw_ini
                this.formData.travelw_end = this.info.travelw_end                
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {            
            e.preventDefault();            
            if(this.validate(this.formData.discount) || this.validate(this.formData.product_type_id) || this.validate(this.formData.product_id) ||
            this.validate(this.formData.bookingw_ini) || this.validate(this.formData.bookingw_end) || this.validate(this.formData.early_booking_days) ||
            this.validate(this.formData.min_nights) || this.validate(this.formData.travelw_ini) || this.validate(this.formData.travelw_end)) {   
                /* this.formData.parent_id = this.formData.parent
                this.formData.country_id = this.formData.country  */               
                
                RateOfferService.addUpdRateOffer(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = false
                this.success_message = true
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getRateOffer(e) {
            console.log(e)
        },
    }
}
