import HotelLocationService from "@/services/hotel/hotel_location.service";

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },     
    name: "CrudHotelLocation",
    components: {
        },
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            formData: {
                //general info
                id: '',
                alias: '',              
            },
        }
    },
    created() {
        this.initForm()
    },
    watch: {
        mode() {
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods:{
        initForm() {
            // For upd action      
            HotelLocationService.getHotelLocation(this.info).then(e => {
                if(this.mode != 'create') {
                    this.formData.id = e.id
                    this.formData.alias = e.alias
                }
            })          
        },
    validate(value) {
        if (value == '')
            return false
        else if(value === undefined)
            return false
        else if(value === null)
            return false
        return true
    },
    submitForm(e) {
        e.preventDefault();            
        if(this.validate(this.formData.alias) ) {
            
            HotelLocationService.addUpdHotelLocation(this.formData, (this.mode == 'create')).then(res => {
                if(res) {
                    this.error_message = false
                    this.success_message = true
                }                    
            })
        } else {
            this.error_message = true
            this.success_message = false
            this.error_message_text = 'Faltan datos por llenar'
        }
    },
    async getHotelLocation(e) {
            console.log(e)
        },
    }   
}
