import '@ztrehagem/vue-accordion/dist/vue-accordion.css'
import VueAccordion from '@ztrehagem/vue-accordion'

// External services
import Services from '@/services/index'
import Authorization from "@/services/user/auth.service"

export default {
    name: "Sidebar",
    components: {
        VueAccordion
    },
    data() {
        return { 
            isExpanded: true,  
            showAccordion: false,      
            navbar:[
                //Nomenclatures
                {
                    name: "Nomencladores",
                    icon: 'cubes',                      //cambiar
                    active: false,
                    secure: Authorization.validateRole(['ROLE_Nomenclator']),
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                            name: "Género",
                            active: false,
                            keyword: "gender",
                            secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                            service: Services.GenderService
                        },{
                            name: "País",
                            active: false,
                            keyword: "country",
                            secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                            service: Services.CountryService,                            
                        },{
                            name: "Moneda",
                            active: false,
                            keyword: "currency",
                            secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                            service: Services.CurrencyService
                    },{
                            name: "Tipo Producto",
                            active: false,
                            keyword: "product_type",
                            secure: Authorization.validateRole(['ROLE_Admin', 'ROLE_Comercial']),
                            service: Services.ProductTypeService
                    }]
                },
                //Autorization
                {
                    name: "Autorización",
                    icon: 'user',
                    active: false,  
                    secure: Authorization.validateRole(['ROLE_User']),
                    submenu: true,
                    activeSubmenu: true,
                    child:[
                        {
                            name: "Usuarios",
                            active: Authorization.validateRole(['ROLE_User', 'ROLE_Agency']),
                            keyword: "client",
                            secure: Authorization.validateRole(['ROLE_User']),
                            service: Services.ClientService
                        },
                        {
                            name: "Roles",
                            active: false,
                            keyword: "roles",
                            secure: Authorization.validateRole(['ROLE_Admin']),
                            service: Services.RolesService
                        }
                    ]
                },
                //Poles
                {
                    name: "Polos",
                    icon: 'street-view',
                    active: false,     
                    secure: Authorization.validateRole(['ROLE_Comercial']),                            
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Polo",
                        active: Authorization.validateRole(['ROLE_Comercial']),
                        keyword: "pole",
                        secure: true,
                        service: Services.PoleService
                    },{
                        name: "Tipo Polo",
                        active: false,
                        keyword: "pole_type",
                        secure: true,
                        service: Services.PoleTypeService,
                    }]
                },                
                //Hotels
                {
                    name: "Hoteles",
                    icon: 'building',
                    active: false,
                    secure: Authorization.validateRole(['ROLE_Prices', 'ROLE_Comercial']),                            
                    submenu: true,
                    activeSubmenu: false,
                    child:[
                        {
                        name: "Hotel",
                        active: false,
                        keyword: "hotel",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelService
                    },{
                        name: "Categoría Hotel",
                        active: false,
                        keyword: "hotel_category",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelCategoryService
                    },{
                        name: "Cadena de Hotel",
                        active: false,
                        keyword: "hotel_chain",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelChainService
                    },{
                        name: "Ubicación Hotel",
                        active: false,
                        keyword: "hotel_location",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelLocationService
                    },{
                        name: "Tipo de Habitación Hotel",
                        active: false,
                        keyword: "hotel_room_type",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelRoomTypeService
                    },{
                        name: "Habitación Hotel",
                        active: false,
                        keyword: "hotel_room",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelRoomService
                    },{
                        name: "Tipo de Categoría de la habitación del hotel",
                        active: false,
                        keyword: "hotel_room_type_category",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelRoomTypeCategoryService
                    },{
                        name: "Segmento Hotel",
                        active: false,
                        keyword: "hotel_segment",
                        secure: Authorization.validateRole(['ROLE_Comercial']),
                        service: Services.HotelSegmentService
                    },{
                        name: "Hotel Room Price",
                        active: false,
                        keyword: "hotel_price",
                        url: "AdminPrices",
                        secure: Authorization.validateRole(['ROLE_Prices']),
                        // service: Services.HotelRoomPriceService
                    }]
                },
                //Prices
                {
                    name: "Precios",
                    icon: 'money-bill',
                    active: false,    
                    secure: Authorization.validateRole(['ROLE_Prices']),                             
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                        name: "Tarifa de Oferta",
                        active: false,
                        keyword: "rate_offer",
                        secure: Authorization.validateRole(['ROLE_Prices']),
                        service: Services.RateOfferService
                    },{
                        name: "Tipo de tarifa",
                        active: false,
                        keyword: "rate_type",
                        secure: Authorization.validateRole(['ROLE_Prices']),
                        service: Services.RateTypeService
                    },
                    {
                        name: "Precio Habitación Hotel",
                        active: false,
                        keyword: "hotel_price",
                        url: "AdminPrices",
                        secure: Authorization.validateRole(['ROLE_Prices']),
                        // service: Services.HotelRoomPriceService
                    }]
                },
                //Galleries
                {
                    name: "Galerías",
                    icon: 'image',
                    active: false, 
                    secure: Authorization.validateRole(['ROLE_Comercial']),                   
                    submenu: true,
                    activeSubmenu: false,
                    child:[{
                            name: "Galería",
                            active: false,
                            keyword: "gallery",
                            secure: Authorization.validateRole(['ROLE_Comercial']),
                            service: Services.GalleryService
                        },{
                            name: "Galería de Filtro",
                            active: false,
                            keyword: "gallery_filter",
                            secure: Authorization.validateRole(['ROLE_Comercial']),
                            service: Services.GalleryFilterService
                        }
                    ]
                },
            ],            
        }
    },
    created() {
        // Actualizando el role del usuario
    },
    methods: {
        async activeItemNavbar(index,subindex=null){   
            let CrudKey, name, keyword, list, url
            if (!this.navbar[index].active) {
                //Show Submenu                                    
                if(this.navbar[index].submenu){
                    //desactive items submenu
                    this.navbar.map(e => {e.active = false})                                                
                    if(subindex != null){
                        //show content submenu                                                
                        this.navbar[index].child.map(e => {e.active = false})                        
                        this.navbar[index].child.active = true   
                        CrudKey = this.navbar[index].child[subindex].keyword
                        name = this.navbar[index].child[subindex].name
                        keyword = this.navbar[index].child[subindex].keyword
                        url = (this.navbar[index].child[subindex].url) ? this.navbar[index].child[subindex].url : 'CrudKey'
                        if(this.navbar[index].child[subindex].service) {
                            list = await this.navbar[index].child[subindex].service.getList()
                        }
                        // console.log('prueba' ,list)
                    } else {
                        //active and desactive submenu
                        this.navbar[index].activeSubmenu = !this.navbar[index].activeSubmenu                        
                    }
                } else {
                //Show Menu
                    CrudKey = this.navbar[index].keyword
                    name = this.navbar[index].name
                    keyword = this.navbar[index].keyword
                    list = this.navbar[index].child[subindex].service.getList()
                }                                   
                if ((this.$route.fullPath != '/admin/prices/' + keyword && this.$route.fullPath != '/admin/' + keyword) && CrudKey) {
                    this.$router.push({ name: url, params: {CrudKey} })
                    this.$emit('setNavBar', name, keyword, list)
                }
                this.$emit('crudStatus')
            }
        },  
    },
}
