import Authorization from "@/services/user/auth.service";
import Services from "@/services/index";

import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

import moment from 'moment'

export default {
    name: "CrudGallery",
    props: {
        info: 0,
        mode: {
            default: 'create'
        }
    },
    components: {
        ModelSelect,
    },
    data: () => ({  
        success_message_text: 'Se salvaron los datos correctamente!!!',
        error_message_text: '',
        error_message: false,
        success_message: false,      
        image_loaded: false,  
        load: false,   
        image_url: '',
        classes: [
            {text: 'Hotel', value: 'hotel'},
            {text: 'Polo', value: 'pole'},
        ],
        className: '',
        obj_list: [],
        formData: {
            id: 0,
            class_name: '',
            id_object_class_list: {
                value: '',
                text: ''
            },
            id_object_class: 0,
            subcategory: '',
            lang: 'all',
            image_name: '',
            image_keyword: '',
            ext: '',
            anno: moment().format('YYYY'),
            order: 0,
            active: 1,
        },                
    }),
    created() {         
        this.initForm()
    }, 
    watch: {
        className(item) {
            this.formData.class_name = item
            this.updObjList()
        }
    },      
    methods: {
        initForm() {
            if (this.mode != 'create') {
                Services.GalleryService.getImage(this.info).then(async e => {
                    this.formData.id = e.id,
                    this.formData.image_name = e.class_name+'_'+e.id,
                    this.className = e.class_name,
                    this.formData.class_name = e.class_name,
                    this.formData.id_object_class_list = e.id_object_class,
                    this.formData.id_object_class = e.id_object_class,
                    this.formData.subcategory = e.subcategory,
                    this.formData.lang = e.lang,
                    this.formData.image_keyword = e.image_keyword,
                    this.formData.ext = e.ext,
                    this.formData.anno = e.anno,
                    this.formData.order = e.order
                    if(e.resources) {                    
                        for(let item of e.resources) {
                            if(item.image == 'original'){
                                this.image_url = item.url
                                this.image_loaded = true
                            }
                        }
                    }   
                    if(e.thumbs) {
                        for(let t of e.thumbs) {
                            this.formData.filters.push({
                                text: t.gallery_filter.filter_keyword,
                                value: t.gallery_filter.id
                            })
                        }
                    } 
                })
            }
        },        
        imageSelected(e) {            
            const file = e.target.files[0];
            this.image_url = URL.createObjectURL(file);
            this.image_loaded = true

            console.log(file)
            this.formData.image_keyword = file.name.split('.')[0]
            this.formData.ext = file.name.split('.')[1]
        }, 
        clearImage(){
            this.image_url = ''
            this.formData.image_keyword = ''
            this.formData.ext = ''
            this.image_loaded = false
            this.$refs.file.value = ''
        },   
        async updObjList() {
            this.$refs.product_load.classList.remove("hidden");
            try {
                if(this.formData.class_name == 'hotel') {                
                    this.obj_list = await Services.HotelService.getSimpleList()
                } else {
                    this.obj_list = await Services.PoleService.getSimpleList()
                }
                this.$refs.product_load.classList.add("hidden");
            } catch(e) {
                console.log(e)
            }
            return this.obj_list
        },                
        submitForm(e) {
            e.preventDefault();           

            if(this.validate(this.formData.class_name) && this.image_url != ''){   
                this.formData.id_object_class = (typeof this.formData.id_object_class_list == 'object') ? this.formData.id_object_class_list.value : this.formData.id_object_class_list                
                
                this.load = true
                const fd = new FormData()

                // agrgando imagen
                const selectedFile = this.$refs.file.files[0]
                if(selectedFile)
                    fd.append('images', selectedFile, selectedFile.name)

                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }
                // console.log(fd)
                Services.GalleryService.addUpdGallery({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true                    
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                    }
                })
                
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
    }
}
