import ProductTypeService from "@/services/common/product_type.service"; 

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },
    name: "CrudProductType",
    components: {        
    },
    data() {
        return {
            error_message_text: '',
            error_message: false,
            success_message: false,
            formData: {
                //general info
                id: '',
                order: '',
                alias: '',
                combination_discount: '',
                travel_agent_discount: '',
                book_more_than_one_discount: '',
                name_class: '',
                id_pyth: '',
                release: '',
                margin: '',
                show_in_newsletter: '',
                pax_info: '',
                active: '',
                db_table_name: '',
                accept_on_request: '',
                accept_payment: '',

            },
        }
    },
    created() {   
        this.initForm()     
    },
    watch: {
        mode() {
            //console.log(this.info)
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            ProductTypeService.getProductType(this.info).then(e => {
                if(this.mode != 'create') {                
                    this.formData.id = e.id
                    this.formData.order = e.order
                    this.formData.alias = e.alias
                    this.formData.combination_discount = e.combination_discount
                    this.formData.travel_agent_discount = e.travel_agent_discount
                    this.formData.book_more_than_one_discount = e.book_more_than_one_discount
                    this.formData.name_class = e.name_class
                    this.formData.id_pyth = e.id_pyth
                    this.formData.release = e.release
                    this.formData.margin = e.margin
                    this.formData.show_in_newsletter = e.show_in_newsletter
                    this.formData.pax_info = e.pax_info
                    this.formData.active = e.active
                    this.formData.db_table_name = e.db_table_name
                    this.formData.accept_on_request = e.accept_on_request
                    this.formData.accept_payment = e.accept_payment
                }
            })
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        submitForm(e) {
            
            e.preventDefault();            
            if(
                this.validate(this.formData.order) || 
                this.validate(this.formData.alias) || 
                this.validate(this.formData.combination_discount) ||
                this.validate(this.formData.travel_agent_discount) || 
                this.validate(this.formData.book_more_than_one_discount)) {                   
                
                ProductTypeService.addUpdProductType(this.formData, (this.mode == 'create')).then(res => {
                    if(res) {
                        this.error_message = false
                        this.success_message = true
                    }                    
                })
            } else {
                this.error_message = false
                this.success_message = true
                this.error_message_text = 'Faltan datos por llenar'
            }
        },
        async getProductType(e) {
            console.log(e)
        },
    }
}
