import moment from 'moment'
import DateRangePicker from 'vue2-daterange-picker'
import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

import { ModelSelect } from 'vue-search-select';
import 'vue-search-select/dist/VueSearchSelect.css'

import AdminTable from '@/components/Admin/AdminTable';

// Services
import HotelService from '@/services/hotel/hotel.service';
import RateTypeService from '@/services/rate/rate_type.service';
import HotelRoomPriceService from '@/services/hotel/hotel_room_price.service';

export default {
    name: "CrudHotelRoomPrice",
    components: {
        ModelSelect,
        DateRangePicker,
        AdminTable
    },
    filters: {
        dateFormat(value) {
        if (value) {
            return moment(String(value)).format('DD MMM YYYY')
        }
        }
    },
    computed: {        
        showData() {
            return (this.dataTable.length > 0)
        }
    },
    data() {
        return {
            emptytable: false,
            spinner: false,
            actions: {delete: true},
            deleteCols: ['tarifa_id'],
            isContract: true,
            submited: false,
            show_error: false,
            error_message: 'Faltan campos obligatorios por rellenar',
            dataTable: [],
            hotels: [],
            rooms: [],
            tariff_types: [],
            hotel: 0,
            tariff: 1,
            dateRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            bookingRange: {
                startDate: moment().add(3, 'days').format('DD MMM YYYY'),
                endDate: moment().add(4, 'days').format('DD MMM YYYY')
            },
            form: {}               
        }
    },
    created() {
        HotelService.getSimpleList().then(h => {this.hotels = h})
        //     this.hotels.push({value: 0, text: '...'})
        //     Array.prototype.push.apply(this.hotels,h)
        // })
        RateTypeService.getSimpleList().then(t => {this.tariff_types = t})
        //     this.tariff_types.push({value: 0, text: '...'})
        //     Array.prototype.push.apply(this.tariff_types,t)
        // })
        this.clearAll()
    },
    watch: {
        hotel(){
            this.form.hotel_id = this.hotel
            
            HotelService.getRoomList(this.hotel).then(rooms => {
                this.rooms = []
                for(let r of rooms){
                    this.rooms.push({
                        text: r.alias,
                        value: r.id,
                    })
                }
            })
        },
        tariff() {
            this.form.tariff_id = this.tariff
            this.isContract = (this.tariff <= 1)
        },
    },
    methods: {
        async getTableInfo() {
            // Search info from table             
            try {
                this.spinner = true
                HotelRoomPriceService.searchRoomPrices(this.form).then(prices => {
                    if(prices) {
                        this.dataTable = (prices) ? prices : []
                    } else {
                        this.emptytable = true
                        setTimeout(() => { this.emptytable = false}, 3000)
                    }
                    this.spinner = false
                })
            } catch(error) {
                console.log(error)
            }
        },
        getRangeDates(objDate) {            
            this.form.date_from = moment(objDate.startDate).format('yyyy-MM-DD')
            this.form.date_to = moment(objDate.endDate).format('yyyy-MM-DD')
        },
        setPrices(e) {
            e.preventDefault(); 
            this.submited = true
            if(this.validateParams()){
                try {
                    HotelRoomPriceService.addPrices(this.form).then(res => {
                        this.dataTable = res
                        setTimeout(() => {
                            this.submited = false                    
                        }, 3000)
                        this.clearAll()
                    })
                } catch(error) {
                    console.log(error)
                }                
            }                
        },
        deletePrices(ids) {            
            try {
                // get all objects to delete
                const tariff_ids = []
                ids.map(e => {                    
                    let obj = this.dataTable.find(d => d.id == e)
                    tariff_ids.push(obj.tarifa_id)
                })
                HotelRoomPriceService.deletePrices({ids,tariff_ids}).then(res => {
                    this.dataTable = res
                    setTimeout(() => {
                        this.submited = false                    
                    }, 3000)
                    this.clearAll()
                })
            } catch(error) {
                console.log(error)
            } 
        },
        clearAll() {
            this.hotel = 0
            this.form = {
                hotel_id: 0,
                room_id: 0,
                tariff_id: 1,    
                date_from: moment().add(3, 'days').format('yyyy-MM-DD'),
                date_to: moment().add(4, 'days').format('yyyy-MM-DD'),
                stop_sale: 0,
                // Special Tariff
                date_booking_ini: '',
                date_booking_end: '',
                early_booking_days: 0,
                min_nights: 1,
                // Prices
                apply_price_per_pax: 0,
                apply_double_price_adult_with_one_child: 0,                
                single_price: 0,
                double_price: 0,
                triple_price: 0,
                first_child_price: 0,
                first_child_free: 0,
                first_child_one_adult_price: 0,
                first_child_one_adult_price_free: 0,                
                second_child_price: 0,
                second_child_free: 0,
                second_child_one_adult_price: 0,
                margin: 25,
            }
        },
        validateParams(){               
            console.log(this.form)
            if(this.form.hotel_id == 0 || this.form.room_id == 0 || this.form.tariff_id == 0 
            || this.form.date_from == '' || this.form.date_to == '' 
            || this.form.double_price == 0 || this.form.margin == 0){
                this.show_error = true
                this.submited = false
                setTimeout(() => {
                    this.show_error = false                    
                }, 3000)
                return false
            }
            return true
        }
    }
}
