import CrudClient from "./common/CrudClient.vue";
import CrudRoles from "./common/CrudRoles.vue";

//Pole
import CrudPole from "./pole/CrudPole.vue";
import CrudPoleType from "./pole/CrudPoleType.vue";

//Common
import CrudCountry from "./common/CrudCountry.vue";
import CrudCurrency from "./common/CrudCurrency.vue";
import CrudGender from "./common/CrudGender.vue";
import CrudProductType from "./common/CrudProductType.vue";

//Gallery
import CrudGallery from "./gallery/CrudGallery.vue";
import CrudGalleryFilter from "./gallery/CrudGalleryFilter.vue";
import CrudGalleryPhotoFilter from "./gallery/CrudGalleryPhotoFilter.vue";
import CrudGalleryI18n from "./gallery/CrudGalleryI18n.vue";

//Hotel
import CrudHotel from "./hotel/CrudHotel.vue";
import CrudHotelCategory from "./hotel/CrudHotelCategory.vue";
import CrudHotelChain from "./hotel/CrudHotelChain.vue";
import CrudHotelRoom from "./hotel/CrudHotelRoom.vue";
import CrudHotelRoomAllotment from "./hotel/CrudHotelRoomAllotment.vue";
import CrudHotelLocation from "./hotel/CrudHotelLocation.vue";
import CrudHotelRoomPrice from "./hotel/CrudHotelRoomPrice.vue";
import CrudHotelRoomType from "./hotel/CrudHotelRoomType.vue";
import CrudHotelRoomTypeCategory from "./hotel/CrudHotelRoomTypeCategory.vue";
import CrudHotelSegment from "./hotel/CrudHotelSegment.vue";
import CrudHotelSegmentHotel from "./hotel/CrudHotelSegmentHotel.vue";

//Rate
import CrudRateOffer from "./Rate/CrudRateOffer.vue";
import CrudRateType from "./Rate/CrudRateType.vue";

export default {
   name: 'Dashboard',
   props: {
      menu: {
         default: 'client'
      },
      modeAction: {
         default: 'create'
      },
      objId: {
         default: 0
      },
   },
   components: {
      CrudClient,    
      CrudRoles,    
      CrudPole,
      CrudPoleType,
      CrudCountry,
      CrudCurrency,
      CrudGender,
      CrudProductType,
      CrudGallery,
      CrudGalleryFilter,
      CrudGalleryPhotoFilter,
      CrudGalleryI18n,
      CrudHotel,
      CrudHotelCategory,
      CrudHotelChain,
      CrudHotelRoom,
      CrudHotelRoomAllotment,
      CrudHotelLocation,
      CrudHotelRoomPrice,
      CrudHotelRoomType,
      CrudHotelRoomTypeCategory,
      CrudHotelSegment,
      CrudHotelSegmentHotel,
      CrudRateOffer,
      CrudRateType,
   },
   data() {
      return {
         error_message_text: '',
         error_message: false,
         success_message: false,
         menuList: {
            client: true,        
            roles: false,
            pole: false,
            pole_type: false,        
            country: false,
            currency: false,
            gender: false,
            product_type: false,
            gallery: false,
            gallery_photo_filter: false,
            gallery_filter: false,
            gallery_i18n: false,
            hotel: false,        
            hotel_category: false,
            hotel_chain: false,
            hotel_room: false,
            hotel_room_allotment: false,
            hotel_location: false,
            hotel_room_type: false,
            hotel_room_type_category: false,
            hotel_segment: false,
            hotel_segment_hotel: false,        
            rate_offer: false,        
            rate_type: false,        
            hotel_price: false,
         },
      }
   },
   created() {
      this.updActiveMenu(this.menu)
   },
   computed: {   
   },  
   methods: {     
      updActiveMenu(view) {
         const keys  = Object.keys(this.menuList)               
         keys.map(item => {
            this.menuList[item] = false
         })
         this.menuList[view] = true         
      },
      messageInfo(info){
         if(info.type == 'error') {
         this.error_message_text = info.text
         this.error_message = true
         this.success_message = false
         } else {
         this.error_message = false
         this.success_message = true
         }
         setTimeout(() => {
         this.error_message = false
         this.success_message = false
         }, 3000);
      }, 
   }
}
